import projectList from "./projectList";



export default function Projects(){

    return(
    <section>
        <h1 className="text-3xl text-center text-shadow-pop-tl">Projects</h1>
        <div className="w-5/6 flex flex-wrap gap-3 justify-center p-5  m-auto ">
            {
                projectList.map((project,key)=>{
                    return <Card key={key} project={project}/>
                })
            }
        </div>
    </section>
)

}


